import React, { Component } from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import { withRouter } from 'react-router';

import './index.scss';

import { requestOpenSource } from '../../actions/OpenSource';
import InlineLoader from '../../components/InlineLoader';
// import Loader from '../../components/Loader';

import { CSSTransition } from 'react-transition-group';
import LazyLoad from 'react-lazyload';

import Imgr from 'react-imgr';
import 'react-imgr/dist/styles.min.css';

class OpenSource extends Component {
	constructor(props) {
		super(props);
		props.requestOpenSource('OpenSource');
	}
	projectOnclick = (projecturl, type = null) => {
		if (type === 'demo') {
			this.props.history.push(`/opensource/${projecturl}`);
		} else {
			window.open(projecturl, '_blank');
		}
	};
	mapProjects(project, index) {
		const {ProjectName, NPM,Github} = project || {}
		// const projectImage = project['image-path']
		// 	? project['image-path']
		// 	: 'https://raw.githubusercontent.com/Anishmprasad/circular-knowledge-graph/master/src/public/images/placeholder@480x420.png';
		return (
			<CSSTransition key={index} timeout={500} classNames='item'>
				<LazyLoad height={500}>
					<article className='card cols-3' key={`project-${index}`}>
						<div className='content-card'>
							<Imgr
								preloadSrc={project['preload-image-path']}
								containerStyle={{
									// width: '100%',
									height: '341px'
								}}
								initialBlur
								scale
								placeholder='https://raw.githubusercontent.com/Anishmprasad/circular-knowledge-graph/master/src/public/images/placeholder@480x420.png'
								src={project['image-path']}
								alt='project_image'
								rel='noopener'
							/>
							{/* <img src={projectImage} alt='project_image' rel='noopener' /> */}
							<div className='content'>
								<h3>{ProjectName}</h3>
								<small>
									{NPM !== 'null' && (
										<span
											className='tag'
											onClick={() => {
												this.projectOnclick(NPM);
											}}
										>
											NPM
										</span>
									)}
									{project.github !== 'null' && (
										<span
											className='tag'
											onClick={() => {
												this.projectOnclick(Github);
											}}
										>
											GitHub
										</span>
									)}
									{project.demo !== 'null' && (
										<span
											className='tag'
											onClick={() => {
												this.projectOnclick(ProjectName, 'demo');
											}}
										>
											Demo
										</span>
									)}
								</small>
							</div>
						</div>
					</article>
				</LazyLoad>
			</CSSTransition>
		);
	}
	render() {
		return this.props.OpenSourceProjects.length !== 0 ? (
			<div className='opensource'>
				<div id='projects' className='project grid-flex'>
					{this.props.OpenSourceProjects.map(this.mapProjects.bind(this))}
				</div>
			</div>
		) : (
			<InlineLoader />
		);
	}
}
function mapStateToProps(state) {
	return {
		OpenSourceProjects: state.OpenSource
	};
}
function mapDispatchToProps(dispatch) {
	return bindActionCreators({ requestOpenSource }, dispatch);
}
export default withRouter(
	connect(
		mapStateToProps,
		mapDispatchToProps
	)(OpenSource)
);
